<template>
  <div class="biz-content">
    <biz-sector-list @sectorClick="getClassBySector" title="选择业务板块" :sectors="sectors"></biz-sector-list>
    <!--业务板块数据展示-->
    <div class="data">
      <div class="title">
        <div class="btn">
          <el-button type="primary" plain size="small" @click="sectorDimension">查看维度切分方式</el-button>
        </div>
      </div>
      <div class="table">
        <el-table :data="bizClassList" row-key="no" stripe :header-cell-style="tableHeader" >
          <el-table-column
            header-align="center"
            v-for="column in columns"
            :key="column.prop"
            :prop="column.prop"
            :label="column.title"
            :min-width="column.width"
            :align="column.align">
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="option">
      <back-button plain>上一步</back-button>
      <back-button plain type="next" @next="customDimension">不接受智能推荐，自定义维度</back-button>
      <back-button plain type="next" @next="confirmBiz">接受智能推荐，提交</back-button>
    </div>

    <!--维度切分方式展示模态框，全屏展示 -->
    <el-dialog title="维度切分方式" :visible.sync="dimensionDialogVisible" width="84%">
        <el-row :gutter="20">
            <el-col :span="6">
              <div class="grid-content bg-purple dimension-dialog">
                  <div class="dimension">
                      <div class="item" v-for="item in dimensionList" :key="item.code" @click="queryDimension(item.code)"><span>{{item.name}}</span></div>
                  </div>
              </div>
            </el-col>
            <el-col :span="18">
              <div class="grid-content bg-purple">
                  <el-table :data="dimensionDetail" stripe :header-cell-style="tableHeader" height="400px">
                    <el-table-column header-align="center" prop="no" label="序号" min-width="50" align="center"></el-table-column>
                    <el-table-column header-align="center" prop="tag" label="标签" min-width="200" align="center"></el-table-column>
                    <el-table-column header-align="center" prop="range" label="标签范围" min-width="250" align="center" v-if="rangeShow"></el-table-column>
                    <el-table-column header-align="center" prop="down" label="从()" min-width="125" align="center" v-if="!rangeShow"></el-table-column>
                    <el-table-column header-align="center" prop="up" label="至(不含)" min-width="125" align="center" v-if="!rangeShow"></el-table-column>
                    <el-table-column header-align="center" prop="a" label="满期赔付率（%）" min-width="140" align="center"></el-table-column>
                    <el-table-column header-align="center" prop="b" label="合并终极赔付率（%）" min-width="160" align="center"></el-table-column>
                    <el-table-column header-align="center" prop="c" label="保费占比（%）" min-width="120" align="center"></el-table-column>
                  </el-table>
              </div>
            </el-col>
          </el-row>
      <!-- <div class="dimension-dialog">
        <div class="dimension">
          <div class="item" v-for="item in dimensionList" :key="item.code" @click="queryDimension(item.code)"><span>{{item.name}}</span></div>
        </div>
        <div class="table">
          <el-table :data="dimensionDetail" stripe :header-cell-style="tableHeader">
            <el-table-column
              header-align="center"
              v-for="column in dimensionColumns"
              :key="column.prop"
              :prop="column.prop"
              :label="column.title"
              :min-width="column.width"
              :align="column.align">
            </el-table-column>
          </el-table>
        </div>
      </div> -->
      <div style="clear: both;"></div>
    </el-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {recommend, getDimensionBySector} from '@/api/individual'

const BackButton = () => import('@/components/BackButton')
const BizSectorList = () => import('./BizSectorList')

export default {
  name: 'Recommend',
  components: {BizSectorList, BackButton},
  data () {
    return {
      rangeShow: true,
      columns: [
        {prop: 'no', title: '业务小类序号', width: 120, headerAlign: 'center', align: 'center'},
        {prop: 'lastYearCount', title: '商业险上年出险次数', width: 170, headerAlign: 'center', align: 'left'},
        {prop: 'lastYearAmount', title: '商业险上年出险金额', width: 170, headerAlign: 'center', align: 'left'},
        {prop: 'age', title: '年龄', width: 120, headerAlign: 'center', align: 'center'},
        {prop: 'insureCombine', title: '投保组合', width: 200, headerAlign: 'center', align: 'left'},
        {prop: 'recommendAutoCoe', title: '推荐自主系数', width: 120, headerAlign: 'center', align: 'center'},
        {prop: 'recommendMarketRate', title: '推荐市场费用率(%)', width: 140, headerAlign: 'center', align: 'center'},
        {prop: 'totalPremium', title: '总保费(最新自主系数) (万元)', width: 190, headerAlign: 'center', align: 'center'},
        {prop: 'premiumAccount', title: '保费占比(%)', width: 100, headerAlign: 'center', align: 'center'},
        {prop: 'uLossRatio', title: '终极赔付率(%)', width: 120, headerAlign: 'center', align: 'center'},
        {prop: 'uLossRatioROB', title: '终极赔付率风险等级分类(%)', width: 220, headerAlign: 'center', align: 'center'},
        {prop: 'estimateCostRate', title: '预估经营成本率(%)', width: 140, headerAlign: 'center', align: 'center'},
        {prop: 'historyAutoCoe', title: '历史自主系数', width: 120, headerAlign: 'center', align: 'center'},
        {prop: 'historyMarketRate', title: '历史市场费用率(%)', width: 140, headerAlign: 'center', align: 'center'},
        {prop: 'historyFullRate', title: '历史满期赔付率(%)', width: 140, headerAlign: 'center', align: 'center'},
        {prop: 'historyLossRate', title: '历史出单终极赔付率(%)', width: 170, headerAlign: 'center', align: 'center'}
      ],
      dimensionColumns: [
        {prop: 'no', title: '序号', width: 50, align: 'center'},
        {prop: 'tag', title: '标签', width: 200, align: 'center'},
        {prop: 'range', title: '标签范围', width: 128, align: 'center'},
        {prop: 'down', title: '从()', width: 128, align: 'center'},
        {prop: 'up', title: '至(不含)', width: 128, align: 'center'},
        {prop: 'a', title: '满期赔付率（%）', width: 140, align: 'center'},
        {prop: 'b', title: '合并终极赔付率（%）', width: 160, align: 'center'},
        {prop: 'c', title: '保费占比（%）', width: 120, align: 'center'}
      ],
      // 板块对应业务小类数据
      bizClassList: [],
      // 当前选中的业务板块id
      currentSector: '',
      dimensionList: [],
      dimensionDetail: [],
      dimensionDialogVisible: false
    }
  },
  created () {
    recommend(this.param).then(response => {
      this.$store.commit('SET_SECTORS', response.data.sectorList)
      this.bizClassList = response.data.sectorList[0].bizClassList
      this.currentSector = response.data.sectorList[0].id
    })
  },
  computed: {
    ...mapState({
      sectors: state => state.individual.sectors,
      param: state => state.individual.param,
      tableHeader: state => state.app.tableHeader
    })
  },
  methods: {
    customDimension () {
      this.$router.push({path: '/admin/individual/declare/custom'})
    },
    confirmBiz () {
      this.$router.push({path: '/admin/individual/declare/config'})
    },
    getClassBySector (id) {
      this.currentSector = id
      const index = this.sectors.findIndex(item => item.id === id)
      this.bizClassList = this.sectors[index].bizClassList
    },
    sectorDimension () {
      getDimensionBySector('').then(response => {
        this.dimensionList = response.data.dimensionList
        this.dimensionDetail = response.data.dimensionList[0].detail
        this.dimensionDialogVisible = true
      })
    },
    queryDimension (code) {
      const index = this.dimensionList.findIndex(item => item.code === code)
      this.rangeShow = this.dimensionList[index].rangeShow
      this.dimensionDetail = this.dimensionList[index].detail
    }
  }
}
</script>

<style scoped>
  .dimension-dialog > .dimension {
    float: left;
    width: 100%;
    height:400px;
    overflow:auto;
    overflow-x:hidden;
    border-bottom: solid 1px #eee;
    border-left: solid 1px #eee;
  }

  .dimension-dialog > .dimension > .item {
    width: 100%;
    height: 47px;
    font-size:12px;
    line-height: 47px;
    border-top: solid 1px #eee;
    border-right: solid 1px #eee;
    cursor: pointer;
    position: relative;
  }
  .dimension-dialog > .dimension > .item:hover{
    background:#F4FAFF;
    font-size:14px;
    border:1px solid #15afff
  }
  .dimension-dialog > .dimension > .item[data-v-8719fd78]:hover::after{
    position:absolute;
    border-top:9px solid transparent;
    border-bottom:9px solid transparent;
    border-left:9px solid #1ea8f0;
    content:" ";
    display:block;
    width:0;
    height:0;
    right: -9px;
    top: 15px;
  }
  .dimension-dialog > .dimension > .item[data-v-8719fd78]:hover::before{
    position:absolute;
    border-top:9px solid transparent;
    border-bottom:9px solid transparent;
    border-left:9px solid #fff;
    content:" ";
    display:block;
    width:0;
    height:0;
    right: -7px;
    top: 15px;
    z-index: 1;
  }

  .dimension-dialog > .dimension > .item > span {
    padding-left: 10px;
  }

  .dimension-dialog > .table {
    float: left;
    width: 75%;
  }

  .dimension-dialog > .table > .el-table {
    border-top: solid 1px #eee;
    border-right: solid 1px #eee;
    border-left: solid 1px #eee;
    margin: 0;
  }

  .my-table-header {
    color: white;
  }

</style>



